@charset "UTF-8";

@import "../scss/variables";

/*-----------Vertical tabs-----------*/

ul.resp-tabs-list {
  margin: 0px;
  padding: 0px;
}

.resp-tabs-list li {
  font-weight: 600;
  font-size: 13px;
  display: inline-block;
  padding: 13px 15px;
  margin: 0 4px 0 0;
  list-style: none;
  cursor: pointer;
  float: left;
}

.resp-tabs-container {
  padding: 0px;
  background-color: transparent;
  clear: left;
}

h2.resp-accordion {
  cursor: pointer;
  padding: 5px;
  display: none;
}

.resp-tab-content {
  display: none;
  padding: 20px 15px 0 15px;
  padding-bottom: 0;
}

.resp-content-active, .resp-accordion-active {
  display: block;
}

.resp-vtabs {
  ul.resp-tabs-list {
    float: none;
    margin-top: 0 !important;
    color: #fff;
  }

  .resp-tabs-list li {
    display: block;
    padding: 20px !important;
    margin: 0 0 4px;
    cursor: pointer;
    float: none;
    margin: 0 auto;
    text-align: center;
    background-color: transparent;
    border: 1px solid #eae8f1 !important;
    border-left: 0 !important;
    border-top: 0 !important;
    border-bottom: 0;
  }

  .resp-tabs-container {
    padding: 0px;
    background-color: transparent;
    clear: none;
    border-bottom: 0 !important;
  }

  .resp-tab-content {
    border: none;
    word-wrap: break-word;
    height: 100%;
  }

  li.resp-tab-active {
    position: relative;
    z-index: 1;
    margin: 0 auto;
    text-align: center;
    border-top: 1px solid;
    border-right: 0 !important;
  }
}

.first-sidemenu li {
  &.active {
    background: #ffffff;
    border-right: 0 !important;

    svg {
      fill: #0162e8;
    }
  }

  &:hover svg {
    fill: #0162e8;
  }
}

.resp-vtabs .resp-tab-active:hover {
  fill: #277aec;
  background: #ffffff;
}

.resp-arrow {
  width: 0;
  height: 0;
  float: right;
  margin-top: 3px;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 12px solid #c1c1c1;
}

h2.resp-tab-active span.resp-arrow {
  border: none;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-bottom: 12px solid #9B9797;
}

.resp-easy-accordion h2.resp-accordion, .resp-tab-content-active {
  display: block;
}

